<template>
  <div class="user-container">
    <!-- 用户基本信息面板 -->
    <div class="user-card">
      <!-- 用户头像、姓名 -->
      <van-cell>
        <!-- 使用 title 插槽来自定义标题 -->
        <template #icon>
          <img :src="$store.state.userPhoto" alt="" class="avatar" />
        </template>
        <template #title>
          <span class="username">{{ $store.state.userName }}</span>
        </template>
        <template #label>
          <van-tag color="#fff" text-color="#007bff">申请认证</van-tag>
        </template>
      </van-cell>
      <!-- 动态、关注、粉丝 -->
      <div class="user-data">
        <div class="user-data-item">
          <span>{{ user.art_count }}</span>
          <span>动态</span>
        </div>
        <div class="user-data-item">
          <span>{{ user.follow_count }}</span>
          <span>关注</span>
        </div>
        <div class="user-data-item">
          <span>{{ user.fans_count }}</span>
          <span>粉丝</span>
        </div>
      </div>
    </div>

    <!-- 操作面板 -->
    <van-cell-group class="action-card">
      <van-cell icon="edit" title="编辑资料" is-link to="/user_edit" />
      <van-cell icon="chat-o" title="小思同学" is-link to="/chat" />
      <van-cell icon="warning-o" title="退出登录" is-link @click="quitFn" />
    </van-cell-group>
  </div>
</template>

<script>
import { userInfoAPI } from "@/api";
import { removeToken } from "@/utils/token";
import { Dialog } from "vant";
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      user: {}, //存放用户个人信息
    };
  },

  async created() {
    const res = await userInfoAPI();
    this.user = res.data.data;
    //两种方法使用mutations,保存到vuex
    // this.$store.commit("SET_USERPHOTO", this.user.photo);
    this.SET_USERPHOTO(this.user.photo);
    this.SET_USERNAME(this.user.name);
  },

  methods: {
    ...mapMutations(["SET_USERPHOTO", "SET_USERNAME"]),
    //退出登录
    quitFn() {
      //弹出确认提示框
      Dialog.confirm({
        title: "是否退出登录",
        message: "这就要走了？不爱我了吗？",
      })
        .then(() => {
          // “确认”操作
          removeToken();
          this.$router.replace("/login");
        })
        .catch(() => {
          //“取消”操作
        });
    },
  },
};
</script>

<style scoped lang="less">
.user-container {
  .user-card {
    background-color: #007bff;
    color: white;
    padding-top: 20px;
    .van-cell {
      background: #007bff;
      color: white;
      &::after {
        display: none;
      }
      .avatar {
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 10px;
      }
      .username {
        font-size: 14px;
        font-weight: bold;
      }
    }
  }
  .user-data {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 14px;
    padding: 30px 0;
    .user-data-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 33.33%;
    }
  }
}
</style>
